<template>
	<view class="messageContent" @click.stop="togoMessageDetail(1,item)">
		<view class="messageTop">
			<view class="messageTopLeft">
				<view class="messageTopLeftD" :class="item.readed==true ?'backgroundD2':'backgroundD1'"> </view>
				<view class="messageTopLeftT">{{item.cardCreateTime}}</view>

			</view>
			<view class="messageTopRight">
				<view class="ims">
					<image v-if="item.dutyLawyerUserImgUrl" :src="item.dutyLawyerUserImgUrl" mode="aspectFill"></image>
					<image v-else src="../../static/images/lxg/touxiang.png" mode="aspectFill"></image>
				</view>
				<text class="">
					{{item.dutyLawyerUserName}}
				</text>

			</view>
		</view>
		<view class="messageContentTitle">
			{{item.cardTitle}}
		</view>
		<view class="messageContentImages action22" v-if="item.logDetails">
			<view class="messageContentImagesT overflow2">
				{{item.logDetails}}
			</view>
		</view>
		<view class="messageContentImages action22" v-if="item.fileShowList!=null">
			<view class="messageContentImagesImg displayRow">
				<image v-for="(items,inde) in item.fileShowList" :src="items.validUrL"></image>
			</view>
		</view>
		<view v-if="item.upFile" class="messageContentImages minheightN displayRow">
			<view class="messageContentImagesT overflow2 minheightFont">
				{{item.dutyLawyerUserName}}律师邀请上传附件
			</view>
			<view class="you333"></view>
		</view>
	</view>
</template>

<script>
	import {
		noticereaded
	} from "@/api/index.js";
	export default {
		props: ['isActive', 'item'],
		data() {
			return {

			};
		},

		components: {},

		computed: {},

		methods: {
			async getnoticereaded(item) {
				if (!item.readed) {
					await noticereaded({
						noticeId: item.dutyLawyerUserId
					}).then(res => {})
				}

			},
			togoMessageDetail(v, item) {
				console.log("------->", v)
				this.getnoticereaded(item)
				if (v === 2) {
					uni.navigateTo({
						url: '/pages/message/uploadAttachments?item=' + JSON.stringify(item)
					})
				} else {
					uni.navigateTo({
						url: '/pages/message/messageDetail?item=' + JSON.stringify(item)
					})
				}

			}
		},

		onReady() {}
	};
</script>
