	.messageContent {
		background-color: #fff;
		padding: 20upx;
		margin-bottom: 20upx;
	}

	.messageTopLeft {
		display: flex;
		flex-direction: row;
		align-items: center;
	}

	.messageTopLeftD {

		width: 16upx;
		height: 16upx;
		border-radius: 50%;
		margin-right: 16upx;
	}

	.messageTop {
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: space-between;
	}

	.messageTopLeftT {
		font-size: 24upx;
		font-family: PingFang SC;
		font-weight: 300;
		color: #666666;
	}

	.messageTopRight {
		font-size: 30upx;
		font-family: PingFang SC;
		font-weight: 400;
		color: #333333;
		display: flex;
		flex-direction: row;
